body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stop-watch {
  border: 30px double gray;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stop-watch__center {
  background-color: gray;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stop-watch__hour-hand {
  transform-origin: bottom;
  background-color: #3b2b2a;
  border-radius: 50%;
  width: 2vmin;
  height: 30vmin;
  margin-bottom: 30vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
}

.stop-watch__minute-hand {
  transform-origin: bottom;
  background-color: #000;
  border-radius: 30%;
  width: 1vmin;
  height: 40vmin;
  margin-bottom: 40vmin;
  animation: 5s linear infinite rotate;
  position: absolute;
}

@keyframes rotate {
  from {
    rotate: none;
  }

  to {
    rotate: 360deg;
  }
}

